




























































































































































































































































































.wrapper {
  margin: 30px 5% 0 5%;
}
.date-picker {
  display: inline-flex;
}

.clear-date-picker {
  margin-top: 7px;
}

.items-per-page-picker {
  margin-left: 7px;
  float: right;
}

.md-layout {
  width: 100%;
  margin-bottom: 5px;
}

.md-layout-item {
  margin-right: 5px;
}

.grid {
  clear: both;
}

.card-wrapper {
  padding: 5px;
  box-sizing: border-box;
}

table {
  width: 100%;
}

#viewToggle {
  margin-top: 16px;
  float: right;
  .md-button {
    margin: 0;
  }
}

#search-input {
  font-size: 1.75em !important;
  margin-top: 16px;
  float: left;
  width: calc(100% - 200px);
  margin-bottom: 16px;
  #search-query-input {
    display: inline-block;
    font-size: 1em;
    line-height: 1em;
    width: 100%;
    max-width: 500px;
    padding: 0;
    margin: 0;
    margin-left: 5px;
    outline: none;
    background: transparent;
    border: none;
    color: inherit;
    border-bottom: 1px solid grey;
    &:focus {
      color: var(--md-theme-default-primary);
      border-bottom: 1px solid var(--md-theme-default-primary);
    }
  }
  #resultHeadline {
    font-size: 1rem;
    display: block;
  }
}
