







































































































































































form {
  display: contents;
  td {
    vertical-align: top;
    padding: 0 8px;
    .md-field {
      margin-top: 0;
      padding-top: 0;
      min-height: initial;
    }
    &:last-of-type {
      width: 80px;
      white-space: nowrap;
    }
    .md-button {
      margin: 0;
    }
  }
}

.changed {
  background-color: lightgoldenrodyellow;
}
