































































































.md-button {
  margin: 0 8px;
}

.paginationWrapper {
  width: 100%;
}

.pagination {
  margin-top: 2rem;
  display: block;
  width: 100%;
  text-align: center
}

#pageNumber {
  height: 40px;
  padding: 0;
  margin: 0;
  input {
    font-size: 1.2em;
    font-weight: bold;
    line-height: 1em;
    padding: 0;
    margin: 0;
    outline: none;
    background: transparent;
    border: none;
    text-align: center;
    max-width: 88px;
    height: 40px;
    width: 100%;
    color: inherit;
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      margin: 0;
    }
  }
  &.md-toggle {
    background: rgba(0, 0, 0, 0.1)
  }
}
